import {
  Divider,
  Drawer,
  IconButton,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { logout } from "../actions/userAction";

const DrawerComp = (log) => {
  const [openDrawer, setopenDrwaer] = useState();
  const dispatch = useDispatch();
  const handleLogout = (e) => {
    e.preventDefault();
    dispatch(logout());
  };
  return (
    <>
      <Drawer open={openDrawer} onClose={() => setopenDrwaer(false)}>
        <List>
          {log.log && (
            <>
              <ListItem>
                <ListItemButton LinkComponent={Link} to="/all-blogs">
                  <ListItemText>All blogs</ListItemText>
                </ListItemButton>
              </ListItem>
              <Divider />
              <ListItem>
                <ListItemButton LinkComponent={Link} to="/my-blogs">
                  <ListItemText>My blogs</ListItemText>
                </ListItemButton>
              </ListItem>
              <Divider />
              <ListItem>
                <ListItemButton LinkComponent={Link} to="/create-blogs">
                  <ListItemText>Write Blogs</ListItemText>
                </ListItemButton>
              </ListItem>
              <Divider />
              <ListItem>
                <ListItemButton onClick={handleLogout}>
                  <ListItemText>Logout</ListItemText>
                </ListItemButton>
              </ListItem>
            </>
          )}
          :
          {!log.log && (
            <>
              <ListItem>
                <ListItemButton LinkComponent={Link} to="/login">
                  <ListItemText>Login</ListItemText>
                </ListItemButton>
              </ListItem>
              <Divider />
              <ListItem>
                <ListItemButton LinkComponent={Link} to="/register">
                  <ListItemText>Register</ListItemText>
                </ListItemButton>
              </ListItem>
            </>
          )}
        </List>
      </Drawer>
      <IconButton
        sx={{ color: "white", marginLeft: "auto" }}
        onClick={() => setopenDrwaer(!openDrawer)}
      >
        <MenuIcon />
      </IconButton>
    </>
  );
};
export default DrawerComp;
