import React, { useEffect } from "react";
import { Box, CardMedia, Typography, Grid } from "@mui/material";

const BlogPage = (blog) => {
  return (
    <>
      <Grid
        item
        display={"flex"}
        flexDirection={"column"}
        justifyContent={"center"}
        alignItems={"center"}
        flexWrap={"wrap"}
        color={"white"}
        gap={5}
      >
        <Typography variant="h3" sx={{ fontFamily: "Cedarville Cursive" }}>
          {blog.blog.blog.title}
        </Typography>
        <Grid
          display={"flex"}
          flexDirection={"column"}
          justifyContent={"center"}
          alignItems={"center"}
          gap={5}
        >
          <Grid
            display={"flex"}
            justifyContent={"center"}
            alignItems={"center"}
            flexWrap={"wrap"}
          >
            <CardMedia
              image={blog.blog.blog.image}
              sx={{ width: " 40vw", minWidth: "250px", height: "40vh" }}
            ></CardMedia>
          </Grid>
          <Typography variant="body1">{blog.blog.blog.description}</Typography>
        </Grid>
      </Grid>
    </>
  );
};

export default BlogPage;
