import React, { useEffect, useState } from "react";
import {
  Box,
  AppBar,
  Toolbar,
  Button,
  Typography,
  Tabs,
  Tab,
  useMediaQuery,
  useTheme,
} from "@mui/material";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { login, logout } from "../actions/userAction";
import DrawerComp from "./DrawerComp";

const Header = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  //global state
  const { isAuthenticated } = useSelector((state) => state.user);
  const theme = useTheme();
  var log = window.localStorage.getItem("isLogged");
  const match = useMediaQuery(theme.breakpoints.down("md"));
  // console.log(match);
  const [val, setVal] = useState();

  const handleLogout = (e) => {
    e.preventDefault();
    dispatch(logout());
  };
  return (
    <>
      <AppBar
        position="sticky"
        // color="secondary"
        sx={{
          backgroundColor: "#1d2023",
        }}
      >
        <Toolbar>
          <Link
            to={"/"}
            color=" none"
            style={{ textDecoration: "none", color: "white" }}
          >
            <Typography variant="h3" sx={{ cursor: "pointer" }}>
              Noted.
            </Typography>
          </Link>

          {match ? (
            <>
              <DrawerComp log={log} />
            </>
          ) : (
            <Box display={"flex"} marginLeft="auto" gap={2}>
              {log && (
                <Tabs
                  textColor="inherit"
                  value={val}
                  onChange={(e, v) => setVal(v)}
                  // sx={{ marginRight: 40 }}
                >
                  <Tab label="All blogs" LinkComponent={Link} to="/all-blogs" />
                  <Tab label="My blogs" LinkComponent={Link} to="/my-blogs" />
                  <Tab
                    label="Write Blogs"
                    LinkComponent={Link}
                    to="/create-blogs"
                  />
                </Tabs>
              )}
              {!log && (
                <>
                  <Button
                    sx={{ color: "white" }}
                    LinkComponent={Link}
                    to="/login"
                  >
                    Login
                  </Button>
                  <Button
                    sx={{ color: "white" }}
                    LinkComponent={Link}
                    to="/register"
                  >
                    Register
                  </Button>
                </>
              )}
              {log && (
                <Button sx={{ color: "white" }} onClick={handleLogout}>
                  Logout
                </Button>
              )}
            </Box>
          )}
        </Toolbar>
      </AppBar>
    </>
  );
};

export default Header;
