import axios from "axios";
export const createBlog =
  (title, description, image, user) => async (dispatch) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
      },
    };
    const { blog } = await axios.post(
      "/api/v1/blog/create-blog",
      {
        title: title,
        description: description,
        image: image,
        user: user._id,
      },
      config
    );
    if (blog) {
      console.log(blog.newBlog);
      alert("Blog successfully created!!");
    }

    if (blog) {
      dispatch({ type: "createBlog", payload: blog });
    }
  };
