import React from "react";
import Header from "./Header";
import { Outlet } from "react-router-dom";

const Layout = () => {
  return (
    <>
      <Header />
      <div
        className="App"
        style={{
          background: "linear-gradient(to right, #526D82, #27374D)",
          // minHeight: "110vh",
          height: "auto",
          // padding: "1px",
          // margin: "10%",
        }}
      >
        <Outlet />
      </div>
    </>
  );
};

export default Layout;
