import React, { useEffect, useState } from "react";
import BlogCard from "../components/BlogCard";
import axios from "axios";
import { Box, Container, Grid } from "@mui/material";
const Blogs = () => {
  // const blogs = await axios.get("/api/v1/blog/all-blogs");
  const [blogs, setBlogs] = useState();

  const getAllBlogs = async () => {
    try {
      const { data } = await axios.get("/api/v1/blog/all-blog");
      if (data?.success) {
        setBlogs(data.blogs);
      }
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getAllBlogs();
  }, []);

  return (
    <>
      {/* <Box display={"flex"} margin={"auto"} gap={"10px"} flexWrap={"wrap"}> */}
      <Container>
        <Grid
          container
          direction={"row"}
          alignItems={"center"}
          justifyContent={"center"}
          // spacing={10}
          rowSpacing={1}
          columnSpacing={10}
          columnGap={0}
        >
          {blogs &&
            blogs.map((blogs) => (
              <Grid item xs={12} sm={6} md={4} width={"auto"}>
                <BlogCard blogs={blogs} allBlogs={false} />
              </Grid>
            ))}
        </Grid>
      </Container>
    </>
  );
};

export default Blogs;
