const user = {
  isAuthenticated: false,
  // user: [],
};
const initialState = user;

export const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case "login":
      return {
        ...state,
        isAuthenticated: true,
        user: action.payload,
      };
    case "register":
      return {
        ...state,
        user: action.payload,
      };
    case "logout":
      return {
        ...state,
        isAuthenticated: false,
      };
    default:
      return {
        ...state,
      };
  }
};
