import React, { useState } from "react";
import { Box, Button, TextField, Typography } from "@mui/material";
import { createBlog } from "../actions/blogAction";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

const WriteBlogs = () => {
  const navigate = useNavigate();
  const user = JSON.parse(window.localStorage.getItem("user"));
  // const id = user._id;
  // const { user } = JSON.par;

  const [vals, setVals] = useState({
    title: "",
    description: "",
    image: "",
  });

  const dispatch = useDispatch();
  const { title, description, image } = vals;

  const handleSubmit = (e) => {
    e.preventDefault();
    dispatch(createBlog(title, description, image, user));
    alert("New blog created");
  };

  const handleChange = (e) => {
    setVals({ ...vals, [e.target.name]: e.target.value });
  };

  return (
    <form onSubmit={handleSubmit}>
      <Box
        display={"flex"}
        flexDirection={"column"}
        maxWidth={650}
        alignItems={"center"}
        margin={"auto"}
        // marginTop={3}
        padding={5}
        justifyContent={"center"}
        boxShadow={"10px #ccc"}
        sx={{
          backgroundColor: "#F1EFEF",
        }}
      >
        <Typography variant="h4" padding={3} textAlign={"center"}>
          Write Blog
        </Typography>
        <TextField
          placeholder="Title"
          name="title"
          margin="normal"
          type="text"
          fullWidth
          value={title}
          onChange={handleChange}
          backgroundColor="white"
        />
        <TextField
          placeholder="So what's on your mind"
          name="description"
          margin="normal"
          type="text"
          multiline
          rows={7}
          fullWidth
          variant="filled"
          value={description}
          onChange={handleChange}
        />
        <TextField
          placeholder="image"
          name="image"
          margin="normal"
          type="text"
          fullWidth
          value={image}
          onChange={handleChange}
        />
        <Button
          type="submit"
          onSubmit={handleSubmit}
          variant="contained"
          sx={{ margin: 3, borderRadius: 5, backgroundColor: "black" }}
        >
          Submit
        </Button>
        <Button sx={{ color: "white", backgroundColor: "black" }}>
          Read what the world is writing
        </Button>
      </Box>
    </form>
  );
};

export default WriteBlogs;
