import React, { useEffect } from "react";
import Card from "@mui/material/Card";
import axios from "axios";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import { useNavigate } from "react-router-dom";

const BlogCard = ({ blogs, allBlogs }) => {
  const navigate = useNavigate();
  const deletePost = async (id) => {
    const { data } = await axios.delete(`api/v1/blog/delete-blog/${id}`);
    if (data.success === true) {
      alert("Blog deleted successfully !!");
      window.location.reload();
    }
  };
  return (
    <Card
      sx={{
        // width: "30%",
        margin: "auto",
        marginTop: "50px",
        boxShadow: "10px  10px #ccc",
        ":hover": {
          boxShadow: "10px 10px 10px #ccc",
        },
      }}
    >
      <CardMedia
        sx={{ height: 140 }}
        image={blogs.image}
        title="green iguana"
      />
      <CardContent>
        <Typography gutterBottom variant="h5" component="div">
          {blogs.title}
        </Typography>
        <Typography variant="body2" color="text.secondary">
          {blogs.description.substring(0, 100) + "..."}
        </Typography>
      </CardContent>
      <CardActions>
        {allBlogs === true ? (
          <Button size="small">By - You</Button>
        ) : (
          <Button size="small">By -{blogs.user.username} </Button>
        )}
        {/* <Button size="small">By - {blogs.user.username}</Button> */}
        <Button
          size="small"
          onClick={() => navigate(`/about-blog/${blogs._id}`)}
        >
          Learn More
        </Button>
        {allBlogs && (
          <Button size="small" onClick={() => deletePost(blogs._id)}>
            Delete
          </Button>
        )}
      </CardActions>
    </Card>
  );
};

export default BlogCard;
